body{
    margin: 0;
    background: white;
}
.cal-container{
    width: 100%;
    display: grid;
    margin-top: 2rem;
    grid-template-columns: repeat(4,18rem);
    grid-template-rows: minmax(5rem, auto) repeat(5,6rem);
    justify-content: center;

}
.span-two{
     grid-column: span 2;
     background-color:#0a29644d;
    font-size: xx-large;
    font-weight: bold;
}
/* input[type='text']{
    color: white;
    box-sizing: border-box;
} */
.span-full{
    grid-column: 1/-1;
    font-size: 2.5rem;
    padding: 0.75rem;
    background-color: rgb(100, 100, 130);
    text-align: right;
    text-decoration-color: white;


}

 .btn{
    background-color:#0a29644d;
    font-size: xx-large;
    font-weight: bold;
}
.btn:hover{
    background-color: white;
}
.btn2{
    background-color: #fc7216 ;
    font-size: xx-large;
    font-weight: bolder;
} 
.btn2:hover{
    background-color: white;
}